#form-product
    div, button
        width: 100%
    h1
        margin-bottom: 1rem
    label
        min-width: 110px
        font-size: 1.1rem
        font-weight: 500
        border-right: 1px solid lightgray
        padding: .5rem 0rem .35rem .5rem
    div
        display: flex
        border: 1px solid lightgray
        border-radius: .25rem
        align-items: center
        margin-bottom: 0.5rem
        box-shadow: 0px 0px 0px rgba(0, 0, 0, 0.5)
        overflow: hidden
        transition: .2s
        &.focus
            transition: .3s
            box-shadow: 0px 0px 7px rgba(0, 0, 0, 0.2)
            border-color: transparent
            transform: scale(1.01)
        input,textarea
            border: none
            padding: 0.5rem 0.5rem 0.35rem
            font-size: 1rem
            width: 100%
            outline: none
            resize: none
    button[type='submit']
        cursor: pointer
        padding: 0.7rem
        padding-bottom: 0.5rem
        border-radius: 0.25rem
        margin-block: 1rem
        background-color: limegreen
        color: white
        border: none
        transition: 0.2s
        font-size: 1.2rem
        p
            color: white
            font-weight: 500
            transition: 0.2s
        &:hover
            transition: 0.3rem
            box-shadow: inset -2px -2px 7px rgba(0, 0, 0, 0.2), 2px 2px 7px rgba(0, 0, 0, 0.2)
            transform: scale(0.9) rotate(1deg)
            p
                transition: 0.2s
                text-shadow: 0px 0px 1px lightgray
    #delete
        font-size: 1.2rem
        font-weight: 500
        display: flex
        width: auto
        margin: auto
        align-items: center
        cursor: pointer
        color: red
        border: none
        background-color: white
            transition: .2s
        &:hover
            transition: .3s
            p
                transition: .3s
                text-shadow: 1px 1px 1px rgba(255,0,0, .5)
                transform: rotate(10deg)
            .delete-icon
                transform: rotate(-25deg)
                transition: .3s
        p
            padding-top: .25rem
            margin-left: .25rem
