@import url('https://fonts.googleapis.com/css2?family=Arima:wght@400;500;600;700&display=swap')
*
    margin: 0rem
    padding: 0rem
    box-sizing: border-box
    line-height: 1
    font-family: 'Arima', cursive
#root
    width: 100vw
    min-height: 100vh
    display: flex
    flex-direction: column
    align-items: center
    justify-content: center
    background-color: #f0f0f7
#app
    // border: 1px solid #e6e6f0
    // border: 1px solid #fff
    padding: 1.25rem
    display: flex
    flex-direction: column
    width: 75%
    min-height: 90vh
    border-radius: .5rem
    background-color: #fff
header
    display: flex
    padding-bottom: 1rem
    border-bottom: 1px solid lightgray
    div
        flex: 1
        align-items: center
        display: flex
    #isAdmin
        justify-content: center
        button
            cursor: pointer
            font-size: 1rem
            font-weight: 600
            background-color: white
            border: none
            opacity: .5
            border-bottom: 2px solid transparent
            transition: .2s
            &.active
                transition: .3s
                opacity: 1
                border-bottom: 2px solid black
                color: black
                // border-bottom: 2px solid #e559f9
                // color: #e559f9
    #cart-btn
        justify-content: flex-end
        .link-cart
            color: black
            text-decoration: none
            padding-inline: .5rem
            border-radius: 5rem
            border: none
            background-color: transparent
            display: flex
            align-items: center
            font-size: 1.7rem
            transition: .2s
            cursor: pointer
            &:hover
                transition: .3s
                color: dodgerblue
                transform: scale(1.05)
main
    flex: 1
    display: flex
    flex-direction: column

.link-form
    align-self: center
    padding: .5rem
    margin-top: 1rem
    font-weight: 600
    border-radius: 100px
    a
        color:  black
        // text-decoration: none
    &:hover
        transition: .3s
        background-color: dodgerblue
    &:hover a
        transition: .3s
        color: white