.card
    user-select: none
    display: flex
    border: 1px solid lightgray
    max-width: 248px
    min-width: 248px
    flex: 1
    border-radius: .25rem
    transition: .2s
    position: relative
    &.hover
        transition: .3s
        border: 1px solid transparent
        box-shadow: 0px 0px 7px rgba(0, 0, 0, 0.2)
    .photo
        width: 85px
        height: 100px
        border: .5rem solid transparent
        background-size: cover
        background-repeat: no-repeat
        background-position: center
        // img
        //     width: 100%
        //     height: 100%
    .info
        display: flex
        flex-direction: column
        justify-content: space-between
        flex: 1
        padding: .5rem
        border-left: 1px solid #ccc
    .name
        font-weight: 600
        font-size: 1.1rem
    .stock
        font-weight: 500
    .price
        font-weight: 500
        text-align: right
    .edit
        position: absolute
        top:0
        right:0
        font-size:  1.1rem
        cursor: pointer
        color: rgba(0,0,0,.5)
        background-color: rgba(200,200,200,.2)
        border-bottom-left-radius: 100px
        border: none
        padding:.2rem .2rem .7rem .7rem
        transition: .2s
        &:hover
            transition: .3s
            background-color: rgba(20,20,20,.1)
            color: rgba(0,0,0,1)