#modal
    .modal-overlay 
        position: fixed
        top: 0
        left: 0
        z-index: 1040
        width: 100vw
        height: 100vh
        background-color: #000
        opacity: .5

    .modal-wrapper 
        position: fixed
        top: 0
        left: 0
        z-index: 1050
        width: 100%
        height: 100%
        overflow-x: hidden
        overflow-y: auto
        outline: 0

    .modal 
        z-index: 100
        background: white
        position: relative
        margin: 1.75rem auto
        border-radius: .5rem
        max-width: 500px
        padding: 1.5rem

    .modal-header 
        display: flex
        justify-content: space-between
        align-items: center
        border-bottom: 1px solid #e5e5e5
        padding-bottom: .5rem
        margin-bottom: 1rem

    .modal-close-button 
        font-size: 1.2rem
        width: 1.8rem
        height: 1.8rem

        font-weight: 700
        line-height: 1
        color: #000
        opacity: .5
        cursor: pointer
        border-radius: 100px
        border: none
        &:hover
            opacity: 1
        .btn-close
            margin-top: .1rem
    button 
        font-size: .9rem
        font-weight: 700
        border: none
        // border-radius: 3px
        // padding: .3rem 1rem
        // margin-left: .5rem

    .button-default 
        background: #247BA0
        color: #fff