#cart
    img
        width: 50px
        height: 50px
    .item-total, .item-quantity
        text-align: center
    .details
        padding-inline: .5rem
        .name
            font-weight: 500
            margin-bottom: .25rem
            font-size: 1rem
        .price
            font-size: .9rem
    .cart-item
        display: flex
        align-items: center
    .no-items
        width: 100%
        letter-spacing: 2px
        font-weight: 500
        height: 50px
        // display: flex
        text-align: center
        vertical-align: middle
